
.sofax-section-padding {
    padding: 130px 0 106px;
    @include max(991px) {
        padding: 100px 0 76px;
    }
    @include max(767px) {
        padding: 80px 0 56px;
    }
}

.sofax-section-padding2 {
    padding: 130px 0;
    @include max(991px) {
        padding: 100px 0;
    }
    @include max(767px) {
        padding: 80px 0;
    }
}
.sofax-section-padding3 {
    padding: 0 0 130px;
    @include max(991px) {
        padding: 0 0 100px;
    }
    @include max(767px) {
        padding: 0 0 80px;
    }
}
.sofax-section-padding4 {
    padding: 200px 0 100px;
    @include max (991px){
        padding: 180px 0 80px;
    }
    @include max (767px){
        padding: 160px 0 60px;
    }
}
.sofax-section-title {
    padding-bottom: 80px;
    @include max(991px) {
        padding-bottom: 60px;
        text-align: center;
    }
    @include max(767px) {
        padding-bottom: 40px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-medium {
            text-align: center;
            max-width: 750px;
            margin: 0 auto;
        }
        &.max-width-large {
            text-align: center;
            max-width: 830px;
            margin: 0 auto;
        }
    }
    
    
}
.sofax-title-btn {
    @include max(991px) {
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    
}

.extra-mt {
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
}
.sofax-default-content {
    h2 {
        margin-bottom: 20px;
    }
    p {
        font-size: 20px;
    }
    @include max(991px) {
        text-align: center;
    }
}




