.sofax-breadcrumb{
  text-align: center;
  padding: 195px 0 130px;
  background-color: green;
  @include max(991px) {
    padding: 170px 0 100px;
  }
  @include max(767px) {
    padding: 170px 0 80px;
  }
  @include max(575px) {
    padding: 130px 0 80px;
  }
}

h1.post__title{
  margin-bottom: 50px;
  color: white;
  @include max(1400px) {
    margin-bottom: 30px;
  }
}

.breadcrumbs{
  ul{
    li{
      font-size: 21px;
      font-weight: 700;
      line-height: 1;
      z-index: 0;
      padding: 0 20px;
      position: relative;
      display: inline-block;
      color: var(--accent-color);
      @include max(767px) {
        font-size: 18px;
      }
      &:first-child{
        padding-left: 0;
      }
      &:last-child{
        padding-right: 0;
      }
      &:before {
        content: "";
        right: -8px;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        position: absolute;
        background-size: 9px;
        background-position: center right;
        background-repeat: no-repeat;
        background-image: url(../images/icon/arrow-right7.svg);
      }
      &:last-child:before{
        content: none;
      }
      a{
        font-weight: 400;
        color: #FDFDE1;
      }
    }
  }
}