.pricing-text {
    ul {
        li{
            position: relative;
            padding-left: 35px;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: #0E0E0E; 
            margin-bottom: 15px;
            img {
                position: absolute;
                left: 0;
            }
        }
    }
}
// end pricing section v1

.sofax-iconbox-icon {
    flex-shrink: 0;
} 
.sofax-content-iconbox-wrap {
    display: flex;
    gap: 20px;
    max-width: 600px;
    margin-bottom: 24px;
}
// sofax content2 section
.sofax-list-icon {
    &-wrap {
        display: flex;
        gap: 20px;
        @include max (767px){
            display: block;
        }
    }
    &-icon {
        ul{
            li{
                position: relative;
                font-size: 23px;
                font-weight: 700;
                line-height: 32px;
                text-align: left;
                color: #0E0E0E;
                padding-left: 30px;
                margin-bottom: 24px;
                img {
                    position: absolute;
                    left: 0;
                    top: 5px;
                  }
                
            }
        }
    }
}


