.accordion-item {
    border: none;
    box-shadow: 0px 4px 80px 0px #00000014;
    border-radius: 10px;
    margin-bottom: 24px;
    h2 {
        margin-bottom: 0;
    }
    h4 {
        margin: 0;
    }
}
.accordion-button:not(.collapsed){
    background-color: transparent;
}
.accordion-button:focus{
    border: none;
}
button.accordion-button {
    box-shadow: none!important;
    padding: 20px;
}
