.sofax-footer-top {
    padding: 130px 0 100px;
    @include max (991px){
        padding: 100px 0 80px;
    }
    @include max (767px){
        padding: 80px 0 60px;
    }
  }
  .sofax-footer-wrap{
    img {
        margin-bottom: 20px;
    }
    p {
        margin-bottom: 24px;
        font-size: 20px!important;
    }
    &.light-color {
        color: var(--white-color);
    }
    &.ml-60{
        @include max (1199px) {
            margin-left: 0;
            max-width: 600px;
        }
    }
  }
 
  .sofax-footer-menu {
    h5 {
        margin-bottom: 24px;
    }
    ul {
        li{
            margin-bottom: 15px;
            a{
                color: var(--body-color);
            }
        }
    }
    @include max (1199px){
        margin-top: 40px;
    }
    &.light-color{
        h5{
            color: var(--white-color);
        }
        ul {
            li{
                a{
                    color: var(--white-color);
                }
            }
        }
    }
   
    &.ml-61{
        margin-left: 60px;
    }
    
  }
 
  .sofax-footer-active {
    margin-left: 60px;
    
    @include max (1199px){
        margin-left: 0px;
    }
  }
  
  a.social-icon img {
    margin-bottom: 15px;
  }


  .sofax-footer-bottom {
    display: block;
    padding: 30px 0;
    border-top: 1px solid var(--light-color);
    &.center{
        text-align: center;
    }
    p {
        margin: 0;
    }
    &.dark-bottom{
        border-top: 1px solid var(--body-color);
        p{
            color: var(--white-color);
        }
    }
  }

  // end sofax footer v1

.sofax-footer {
    @include max(991px) {
        margin-left: 30px;
    }
    @include max(991px) {
        margin-left: 0;
    }
}

// end sofax footer v2
.sofax-footer3 {
    &-wrap {
        display: flex;
        justify-content: space-between;
        padding: 30px 0;
        border-top: 1px solid var(--light-color);
        @include max (767px){
            display: block;
            text-align: center;
        }
    }
    &-text{
        align-items: center;
        display: flex;
        @include max (767px){
            display: block;
            margin: 15px 0 0 30px;
        }
    }
    p {
        line-height: 28px;
        color: var(--grey-color);
    }
    
}