.sofax-content-shape {
    position: absolute;
    top: -16%;
    right: -19%;
    @include max (1399px) {
        top: -20%;
        right: -23%;
    }
    @include max (1199px) {
        top: -23%;
        right: -27%;
    }
    
    @include max (991px) {
        display: none;
    }
}

.sofax-hero {
    &-shape {
        position: absolute;
        left: -2%;
        bottom: 102%;
        @include max (1399px) {
            left: -7%;
            bottom: 102%;
        }
        @include max (1199px) {
            left: -7%;
            bottom: 105%;
            width: 20%;
        }
        @include max (991px) {
            display: none;
        }
    }
   
    // end sofax hero shape one v1
    &-shape2 {
        position: absolute;
        right: -13%;
        bottom: 67%;
        @include max (1399px) {
            right: -15%;
            bottom: 63%;
        }
        @include max (1199px) {
            right: -11%;
            top: 6%;
            width: 20%;
        }
        @include max (991px) {
            display: none;
        }
    }
}
// cta shape v1
.sofax-cta-shape {
    position: absolute;
    bottom: 3%;
    right: -17%;
    @include max(1349px) {
        right: -4%;
        width: 30%;
