    }
}
// hero shape v2

.hero2-shape {
    @include max (991px) {
        display: none;
    }
}
// sofax cta shape v2

.sofax-cta-shape2 {
    position: relative;
}
.sofax-cta-shape-v2 {
    position: absolute;
    bottom: -20px;
    left: 28%;
    @include max(1199px) {
        left: 38%;
    }
    @include max(991px) {
        left: 67%;
    }
    img {
        @include max (991px){
            width: 90%;
        }
        @include max (767px){
            display: none;
        }
    }
}

