/*  sofax services section v3 */
.sofax-service-iconbox {
    &-wrap {
        display: flex;
        background-color: #fff;
        border-radius: 5px;
        padding: 30px;
        margin-bottom: 24px;
    }
    &-data h4 {
        margin-bottom: 15px;
    }
    &-data p {
        font-size: 20px;
    }
    &-icon {
        flex-shrink: 0;
    }
}
// sofax services v4
.sofax-service-table{
    &-item{
        display: flex;
        padding: 35px 0;
        border-bottom: 1px solid #EAEDF0;
        &:first-child{
            padding-top: 0;
        }
        &:last-child{
            padding-bottom: 0;
            border: none;
        }
        @include max(991px) {
            display: block;
        }
        &:hover  .sofax-default-btn.outline-btn{
            background-color: var(--accent-color);
            border: 1px solid var(--accent-color);
        }
    }
    &-title{
        flex: 0 0 36%;
        h3{
            margin: 0;
            font-size: 35px;
            @include max(1199px) {
                font-size: 30px;
            }
        }
    }
    &-body{
        flex: 0 0 45%;
        padding: 0 30px;
        @include max(991px) {
            padding: 24px 0;
        }
        p{
            font-size: 20px;
        }
    }
    &-btn{
        flex: 0 0 19%;
        text-align: right;
        @include max(991px) {
            text-align: left;
        }
        .sofax-default-btn.outline-btn{
            padding: 17px 60px;
        }
    }
}