.sofax-blog {
    &-wrap-v4 {
        box-shadow: 0px 4px 80px 0px #00000014;
        border-radius: 10px;
        background: #F9F9FA;
        margin-bottom: 24px;
        padding: 20px;
        transition: all 0.4s;
        cursor: pointer;
        &:hover {
            background-color: var(--white-bg);
        }
    }
    &-meta{
        display: flex;
        align-items: center;
        gap: 24px;
        padding: 24px 0 15px 0;
        img {
            @include max (991px){
                width: 100%;
            }
        }
    }
    &-content h4 {
        margin-bottom: 30px;
    }
    &-img {
        img{
            width: 100%;
        }
        @include max (991px){
            text-align: center;
        }
    }
}


