.sofax-iconbox{
    &-wrap {
        border-radius: 10px;
        background-color: var(--white-bg);
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-icon {
        margin-bottom: 30px;
    }
    &-data {
        h4 {
            margin-bottom: 15px;
        }
        p {
            font-size: 20px;
        }
    }
    // end icon box one v1
    &-wrap2 {
        display: flex;
        gap: 12px;
        margin-bottom: 24px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-icon2 {
        flex-shrink: 0;
    }
    &-data2 {
        h4 {
            margin-bottom: 12px;
        }
        p {
            font-size: 20px;
        }
    }
}


