// fonts variable
$body-font: 'Inter', sans-serif!default;
$heading-font: 'DM Sans', sans-serif !default;


// color variable
:root {
  --heading-color: #0E0E0E;
  --body-color: #3C3C3C;
  --accent-color: #E5ABF3;
  --light-bg: #F9F9FA;
  --dark-bg: #0E0E0E;
  --white-bg: #fff;
  --white-color: #fff;
  --light-color: #EAEDF0;
  --light-color2: #DBDBDB;
  --green-bg: #0C9347;
  --grey-color:  #747474;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 24px;
$button_padding: 18px 34px;
$button_font_weight: 700;
$button_font_family: $body-font;

