
.sofax-hero-video {
    color: var(--white-bg);
    background-color: #0E0E0E;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    padding: 18px 30px;
    border-radius: 50px;
    display: inline-block;
    &:hover {
        color: var(--white-bg);
    }
    img {
        margin-right: 10px;
        margin-bottom: 2px;
    }
}
a.sofax-video-popup.video-init {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
//  sofax hero 5
.sofax-hero-video-btn {
    position: absolute;
    top: 0;
    left: 0;
}
//  sofax hero5 video
a.sofax-hero5-video.play-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
        box-shadow: none !important;
        @include max(1199px){
            width: 80%;
        }
        @include max(767px){
            width: 50%;
        }
    }
}