
  /* end  sofax pricing section */

  .sofax-pricing{
    &-wrap{
        border-radius: 10px;
        padding: 40px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color:  var(--white-bg);
        @include max(991px) {
            padding: 30px;
        }
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
        &.active{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-header{
        margin-bottom: 15px;
        img{
            margin-bottom: 30px;
        }
    }
    &-price{
        gap: 10px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid var( --light-color);
    }
    &-body{
        margin-bottom: 40px;
        h6{
            font-weight: 600;
            margin-bottom: 20px;
        }
        ul{
            li{
                position: relative;
                font-weight: 600;
                padding-left: 40px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 5px;
                }
            }
        }
    }
    &-bodyv4 {
        margin-bottom: 0!important;
    }
  }
//  sofax pricing section v4

