.sofax-features {
  &-box {
    border: 1px solid var(--light-color);
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 24px;
    transition: all .4s;
  }
  &-icon {
    margin-bottom: 30px;
  }
  &-content {
    h4{
      margin-bottom: 15px;
    }
    p {
      font-size: 20px;
    }
  }

}
.features-v2 {
  background-color: var(--accent-color);
  padding: 30px 30px 0 30px;
}
.features-v5{
  border: 1px solid var(--dark-bg)!important;
  &:hover{
    box-shadow: 5px 5px 0 0 #0e0e0e;
  }
}