// start form section v4

.form-content-box {
  background: var(--white-color);
  padding: 30px;
  border-radius: 10px;
  @include max (991px){
    margin-top: 50px;
  }
  h3 {
    margin-bottom: 30px;
  }
  input{
    width: 100%;
    border-radius: 50px;
    padding: 18px 24px;
    height: 60px;
    border: 1px solid var(--light-color2)!important;
    outline: none;
    &.button-text {
      height: 150px;
      border-radius: 10px !important;
      padding: 15px 0px 105px 20px !important;
      margin-bottom: 24px;
    }
  }
  .form-field-item {
    margin-bottom: 24px;
  }
  ul.nav {
    background: var(--accent-color);
    border-radius: 50px;
    height: 60px;
    li a {
      color: var(--dark-bg)!important;
      font-weight: 700;
      font-size: 16px;
      line-height: 24px;
      padding: 18px 42px;
      
    }
  }
}

